#Contenedor-TablaSubsidio{
    margin-top: 40px;
    background: #FFFFFF;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.13);
    border-radius: 9px;
    width: 100%;
    padding: 50px;
}

#Cabecera-TablaSubsidio{
    background: #E4E6EB;
}

#Titulo-Cabecera-TablaSubsidio{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
    color: #6E7A9D;
    /* text-align: -webkit-right; */
}

#Total-TablaSubsidio{
    background: #ECF1FA;
    width: 100%;
    height: 40px;
}

#Texto-Total-TablaSubsidio{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
    color: #6E7A9D;
    padding-left: 40px;
    display: flex;
    align-items: center;
}

#Agrupado-TablaSubsidio{
    background: #A3DCEF;
    width: 100%;
    height: 40px;
    border-top: 5px solid white;
    /* border-bottom: 5px solid white; */
}

#Texto-Agrupado-TablaSubsidio{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
    color: #6E7A9D;
    padding-left: 40px;
    display: flex;
    align-items: center;
}

#Icono-Agrupado-TablaSubsidio{
    position: absolute;
    left: 15px;
    color: #6E7A9D;
}

#Texto-Contenido-TablaSubsidio{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
    color: #4D4D4D;
}